// @mui
import { useState, useEffect, useRef } from 'react';
// @mui
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
// components
import { useSettingsContext } from 'src/components/settings';
import { Button, Chip, Stack, Box, TextField, InputAdornment, Modal, Grid, IconButton, Autocomplete, Select, FormControl, InputLabel, MenuItem, Menu, Dialog, DialogTitle, DialogContent, DialogActions } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import SearchIcon from '@mui/icons-material/Search';
import dayjs from 'dayjs';
import { DemoContainer } from '@mui/x-date-pickers/internals/demo';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { SingleInputDateRangeField } from '@mui/x-date-pickers-pro/SingleInputDateRangeField';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import DataTable from 'react-data-table-component';
import { DatePicker } from '@mui/x-date-pickers';
import DeleteIcon from '@mui/icons-material/Delete';
import PercentIcon from '@mui/icons-material/Percent';
import { color, common, decryptToken, formatDate, formatObj } from 'src/utils/common';
import { constants } from 'src/utils/constant';
import axios, { endpoints } from 'src/utils/axios';
import { useSnackbar } from 'src/components/snackbar';
import { useAuthContext } from 'src/auth/hooks';

// ----------------------------------------------------------------------
const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: '75%',
  borderRadius: '10px',
  height: "90%",
  overflowY: 'auto',
  boxShadow: 24,
  display: "flex",
  flexDirection: "column",
  justifyContent: "space-between",
  bgcolor: color.modalbackground,
  gap: '10px',
  p: 2,
};

const styleForRecordPayment = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: '400px',
  bgcolor: color.modalbackground,
  borderRadius: '10px',
  height: "auto",
  boxShadow: 24,
  p: 2,
};
// ----------------------------------------------------------------------
const initialPurchaseState = {
  vendor: null,
  purchaseDate: null,
  purchaseDueDate: null,
  referenceNumber: '',
  products: [],
  offer: '',
  amountToPay: '',
  totalAmount: 0,
  productDiscount: 0,
  paymentMode: '',
  paymentStatus: '',
  bank: '',
};

const initialRecordPaymentState = {
  id: '',
  name: '',
  balance: 0,
  amountToPay: 0,
  paymentMode: '',
  bank: '',
  paymentDate: dayjs(new Date()),
};

const initialPurchaseProduct = {
  category: 'All Categories',
  product: null,
  quantity: '',
  netAmount: null,
  discount: null,
  sellingPrice: null,
};

export default function PurchaseOrderPageView() {

  const settings = useSettingsContext();
  const { enqueueSnackbar } = useSnackbar();
  const [barcodeValue, setBarcodeValue] = useState('');
  const [tempList, setTempList] = useState([])
  const { user } = useAuthContext();
  const [value, setValue] = useState(() => [
    dayjs('2022-04-17'),
    dayjs('2022-04-21'),
  ]);
  const autocompleteRef = useRef(null);

  const [open, setOpen] = useState(false);
  const handleOpen = () => {
    setOpen(true);
    setEditPurchase(false);
    //  Set the purchase date to now
    setPurchase((prevSales) => ({ ...prevSales, purchaseDate: dayjs(new Date()) }));
  };
  const handleClose = () => {
    handleClearAllStates();
    setEditPurchase(false);
    setOpen(false)
  };

  const [purchase, setPurchase] = useState(initialPurchaseState);
  const [vendors, setVendors] = useState([]);
  const [categories, setCategories] = useState([]);
  const [banks, setBanks] = useState([]);
  const [products, setProducts] = useState([]);
  const [offers, setOffers] = useState([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [purchaseProduct, setPurchaseProduct] = useState(initialPurchaseProduct)
  const [isPurchasePageLoading, setIsPurchasePageLoading] = useState(false)
  const [purchaseOrderList, setPurchaseOrderList] = useState([])
  const [purchaseSummary, setPurchaseSummary] = useState({
    totalPurchaseAmount: 0,
    totalPaidAmount: 0,
    pendingAmount: 0
  });
  const [openDialog, setOpenDialog] = useState(false);

  // For Actions Dropdown
  const [anchorEl, setAnchorEl] = useState(null);
  const openMenu = Boolean(anchorEl);
  const [tempData, setTempData] = useState({});

  // RECORD PAYMENTS
  const [recordPaymentModal, setRecordPaymentModal] = useState(false);
  const [recordPayment, setRecordPayment] = useState(initialRecordPaymentState);
  const [editPurchase, setEditPurchase] = useState(false);

  const fetchVendors = async () => {

    try {
      const decryptedToken = localStorage.getItem(constants.keyUserToken);
      const accessToken = decryptToken(decryptedToken);
      const response = await axios.post(endpoints.vendor.list, {}, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      });
      if (response) {
        setVendors(response.data.data);
      }
    } catch (err) {
      enqueueSnackbar('Failed to fetch vendors!', { variant: 'error' });
    }
  };

  const fetchPurchaseSummary = async () => {
    try {
      const decryptedToken = localStorage.getItem(constants.keyUserToken);
      const accessToken = decryptToken(decryptedToken);
      const response = await axios.get(endpoints.purchase.summary, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      });
      if (response) {
        setPurchaseSummary({
          totalPurchaseAmount: response.data.data.totalPurchaseAmount,
          totalPaidAmount: response.data.data.totalPaidAmount,
          pendingAmount: response.data.data.pendingAmount
        })
      }
    } catch (err) {
      enqueueSnackbar('Failed to fetch purchase summary!', { variant: 'error' });
    }
  };

  const fetchProducts = async () => {

    try {
      const decryptedToken = localStorage.getItem(constants.keyUserToken);
      const accessToken = decryptToken(decryptedToken);
      const response = await axios.post(endpoints.product.list, {}, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      });
      if (response) {
        setProducts(response.data.data);
      }
    } catch (err) {
      enqueueSnackbar('Failed to fetch products!', { variant: 'error' });
    }
  };

  const fetchProductsCategory = async () => {

    try {
      const decryptedToken = localStorage.getItem(constants.keyUserToken);
      const accessToken = decryptToken(decryptedToken);
      const response = await axios.post(endpoints.product.category.list, {}, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      });
      if (response) {
        setCategories(response.data.data);
      }
    } catch (err) {
      enqueueSnackbar('Failed to fetch Categories!', { variant: 'error' });
    }
  };

  const fetchPurchase = async () => {

    try {
      const decryptedToken = localStorage.getItem(constants.keyUserToken);
      const accessToken = decryptToken(decryptedToken);
      const response = await axios.post(endpoints.purchase.list, {}, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      });
      if (response) {
        setIsPurchasePageLoading(false);
        setPurchaseOrderList(response.data.data);
        setTempList(response.data.data);
      }
    } catch (err) {
      enqueueSnackbar('Failed to fetch purchase orders!', { variant: 'error' });
      setIsPurchasePageLoading(false);
    }
  };

  const fetchOffers = async () => {

    try {
      const decryptedToken = localStorage.getItem(constants.keyUserToken);
      const accessToken = decryptToken(decryptedToken);
      const response = await axios.post(endpoints.offer.list, { status: ['LIVE'] }, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      });
      if (response) {
        setOffers(response.data.data);
      }
    } catch (err) {
      enqueueSnackbar('Failed to fetch offers!', { variant: 'error' });
    }
  };

  const fetchBanks = async () => {

    try {
      const decryptedToken = localStorage.getItem(constants.keyUserToken);
      const accessToken = decryptToken(decryptedToken);
      const response = await axios.post(endpoints.bank.list, {}, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      });
      if (response) {
        setBanks(response.data.data);
      }
    } catch (err) {
      enqueueSnackbar('Failed to fetch bank accounts!', { variant: 'error' });
    }
  };

  useEffect(() => {
    setIsPurchasePageLoading(true);
    fetchPurchase();
    fetchPurchaseSummary();
    fetchVendors();
    fetchProducts();
    // fetchProductsCategory();
    fetchOffers();
    fetchBanks();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const [startDate, setStartDate] = useState(dayjs().startOf('month'));
  const [endDate, setEndDate] = useState(dayjs().endOf('month'));

  const handleStartDateChange = (date) => {
    setStartDate(date);
  };

  const handleEndDateChange = (date) => {
    setEndDate(date);
  };

  useEffect(() => {
    if (startDate && endDate) {
      const filteredSales = tempList.filter((sale) => {
        const saleDate = dayjs(sale.createdAt);
        console.log(startDate, endDate)
        return saleDate.isBetween(startDate, endDate, null, '[]');
      });
      setPurchaseOrderList(filteredSales);
    } else {
      setPurchaseOrderList(tempList);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [startDate, endDate])


  const handleRecordPayment = () => {
    const recordPaymentPayload = {};

    recordPaymentPayload.id = recordPayment.id;
    recordPaymentPayload.amountToPay = recordPayment.amountToPay;
    recordPaymentPayload.paymentDate = dayjs(recordPayment.paymentDate).format('YYYY-MM-DD');
    recordPaymentPayload.paymentMode = recordPayment.paymentMode;
    if (recordPayment.bank.length > 0) {
      recordPaymentPayload.bank = recordPayment.bank;
    };

    // If no errors, proceed with saving
    const decryptedToken = localStorage.getItem(constants.keyUserToken);
    const accessToken = decryptToken(decryptedToken);
    axios.post(endpoints.purchase.recordPayment, recordPaymentPayload,
      {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      })
      .then((res) => {
        enqueueSnackbar('Purchase order payment recorded successfully!', { variant: 'success' });
        setRecordPaymentModal(false);
        setRecordPayment(initialRecordPaymentState);
        fetchPurchase();
        fetchPurchaseSummary();
      }).catch((err) => {
        enqueueSnackbar(err.error, { variant: 'error' });
      })
  }

  const handleOpenRecordPaymentModal = () => {
    setAnchorEl(null);
    setRecordPaymentModal(true);
    setRecordPayment((prev) => ({
      ...prev, name: tempData.vendorId.name,
      balance: tempData.remainingAmount,
      id: tempData._id
    }));
    setTempData({});
  }

  const handleClearAllStates = () => {
    setPurchase(initialPurchaseState);
    setPurchaseProduct(initialPurchaseProduct);
    setPurchase((prev) => ({ ...prev, vendor: null }));
  };

  const handleDeleteProduct = (row, index) => {
    console.log('deleting product...')
    const newProducts = [...purchase.products];
    newProducts.splice(index, 1);
    setPurchase((prev) => ({ ...prev, products: newProducts }));
  };

  const handleProductChange = (index, field, fieldValue) => {

    // Create a copy of the products array
    const updatedProducts = [...purchase.products];

    // Access the specific product by index
    const product = { ...updatedProducts[index] };

    // Update the field with the new value
    product[field] = fieldValue;

    // Update the products array with the modified product
    updatedProducts[index] = product;

    console.log("new Product", updatedProducts[index]);


    // Update the state immutably
    setPurchase(prevSales => ({
      ...prevSales,
      products: updatedProducts
    }));
  };

  console.log("purchase.products", purchase.products);
  const handleEdit = () => {
    setAnchorEl(null);
    console.log({ tempData });

    setPurchase(prevSales => ({
      ...prevSales,
      vendor: tempData.vendorId,
      purchaseDate: dayjs(tempData.purchaseDate),
      purchaseDueDate: dayjs(tempData.purchaseDueDate),
      products: tempData.products,
      paymentMode: tempData.payments[tempData.payments.length - 1].paymentID.paymentMode,
      paymentStatus: tempData.paymentStatus,
      // ...(tempData?.discount?.offer && { offer: tempData.discount.offer.id }) // Conditionally add the offer object
    }));
    setEditPurchase(true);
    setOpen(true);
  };

  const handleConfirmDelete = () => {

    const decryptedToken = localStorage.getItem(constants.keyUserToken);
    const accessToken = decryptToken(decryptedToken);
    const config = {
      headers: {
        Authorization: `Bearer ${accessToken}`
      }
    };
    axios.post(endpoints.purchase.delete, { id: tempData._id }, config)
      .then((res) => {
        if (res) {
          enqueueSnackbar('Purchase order deleted successfully!', { variant: 'success' });
          setOpenDialog(false);
          setTempData({});
          fetchPurchase();
          // fetchPurchaseSummary(); TODO
        };
      }).catch((err) => {
        console.log(err)
        enqueueSnackbar(err.msg || err.error, { variant: 'error' });
      })
  };

  const addProductToSales = (newValue) => {
    console.log("ðŸš€ ~ addProductToSales ~ newValue:", newValue)

    // Check if the product is already added to purchase
    if (purchase.products.find((product) => product.name === newValue.name)) {
      enqueueSnackbar('Product already added!', { variant: 'error' });
      return;
    }

    // Check if product quantity is available
    const existingProduct = products.find((product) => product.name === newValue.name);
    if (existingProduct && existingProduct.quantity < newValue.quantity) {
      enqueueSnackbar('Product quantity not available!', { variant: 'error' });
      return;
    }

    // Add product to purchase
    const productToAdd = {
      productId: newValue._id,
      name: newValue.name,
      sellingPrice: newValue.purchasePrice,
      quantity: 1,
      sgst: newValue.sgst || '',
      cgst: newValue.cgst || '',
      igst: newValue.igst || '',
    };
    console.log("ðŸš€ ~ addProductToSales ~ productToAdd:", productToAdd)

    setPurchase((prev) => ({
      ...prev,
      products: [...prev.products, productToAdd]
    }));

    console.log(newValue);
    setPurchaseProduct(initialPurchaseProduct);
    if (autocompleteRef.current) {
      autocompleteRef.current.blur();
    };
  };

  const handleSavePurchaseOrder = () => {
    if (parseFloat(purchase.amountToPay) <= 0) {
      enqueueSnackbar('Amount to pay must be greater than 0!', { variant: 'error' });
      return;
    };

    const purchasePayload = { ...purchase };
    purchasePayload.totalAmount = Number(totalAmount.toFixed(2));
    purchasePayload.purchaseDueDate = dayjs(purchasePayload.purchaseDueDate).format('YYYY-MM-DD');
    purchasePayload.purchaseDate = dayjs(purchasePayload.purchaseDate).format('YYYY-MM-DD');
    purchasePayload.amountToPay = parseFloat(purchasePayload.amountToPay).toFixed(2);
    delete purchasePayload.offer;
    delete purchasePayload.purchaseDueDate;
    console.log(purchasePayload);
    // If no errors, proceed with saving
    const decryptedToken = localStorage.getItem(constants.keyUserToken);
    const accessToken = decryptToken(decryptedToken);
    axios.post(endpoints.purchase.add, purchasePayload,
      {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      })
      .then((res) => {
        enqueueSnackbar('Purchase order created successfully!', { variant: 'success' });
        setOpen(false);
        handleClearAllStates();
        fetchPurchase();
        fetchPurchaseSummary();
      }).catch((err) => {
        enqueueSnackbar(err.error || 'Something went wrong!', { variant: 'error' });
      })

  };

  const handleUpdatePurchaseOrder = () => {

    const updatePayload = {};

    updatePayload.paymentStatus = purchase.paymentStatus;
    updatePayload.id = tempData._id;
    console.log({ updatePayload });

    // // If no errors, proceed with saving
    const decryptedToken = localStorage.getItem(constants.keyUserToken);
    const accessToken = decryptToken(decryptedToken);
    axios.post(endpoints.purchase.update, updatePayload,
      {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        }
      })
      .then((res) => {
        enqueueSnackbar('Purchase order updated successfully!', { variant: 'success' });
        setOpen(false);
        setTempData({});
        handleClearAllStates();
        fetchPurchase();
        fetchPurchaseSummary();
        setEditPurchase(false);
      }).catch((err) => {
        enqueueSnackbar(err.error || 'Something went wrong!', { variant: 'error' });
      })
  }

  const columns = [
    {
      name: 'Vendor Name',
      selector: row => row.vendorId.name,
    },
    {
      name: 'Product Name',
      width: '150px',
      selector: row => row.products.length > 1 ? `${row.products[0].name}, & ${row.products.length - 1} more` : row.products[0].name,
    },
    {
      name: 'Amount',
      selector: row => row.totalAmount.toFixed(2),
    },
    {
      name: 'Payment Mode',
      selector: row => row.payments[0].paymentID.paymentMode,
    },
    {
      name: 'Payment Status',
      selector: row => row.paymentStatus,
    },
    {
      name: 'Invoice Number',
      width: "160px",
      selector: row => row.invoiceNumber,
    },
    {
      name: 'Last Updated',
      width: "250px",
      selector: row => formatDate(row.updatedAt),
      sortable: 'true'
    },
    {
      name: 'Actions',
      width: "130px",
      cell: (row) => (
        <>
          <Button onClick={(event) => {
            setAnchorEl(event.currentTarget);
            setTempData(row)
          }}
            disabled={!user?.permissions?.purchase?.u}
            style={{ color: color.actionButton }} variant='outlined' endIcon={<ArrowDropDownIcon />}>Action</Button>
          <Menu
            id="basic-menu"
            anchorEl={anchorEl}
            open={openMenu}
            onClose={() => {
              setAnchorEl(null);
              setTempData({});
            }}
            MenuListProps={{
              'aria-labelledby': 'basic-button',
            }}
          >
            <MenuItem onClick={handleEdit}>Edit</MenuItem>
            <MenuItem onClick={() => { }}>Send via Email</MenuItem>
            <MenuItem onClick={() => { }}>Send via Whatsapp</MenuItem>
            <MenuItem onClick={() => {
              setOpenDialog(true);
              setAnchorEl(null);
            }}>Delete</MenuItem>
            {
              tempData.remainingAmount > 0 &&
              <MenuItem onClick={() => handleOpenRecordPaymentModal()}>Record Payment</MenuItem>
            }
          </Menu>
        </>
      ),
    },
  ];

  const productColumns = [
    {
      name: 'Product Name',
      selector: row => row.name,

    },
    {
      name: 'Quantity',
      selector: row => row.quantity,
      cell: (row, index) => (
        <TextField
          disabled={editPurchase}
          type='number'
          autoFocus={index === (purchase.products.length - 1)}
          onBlur={(e) => {
            // Create a copy of the products array
            const updatedProducts = [...purchase.products];

            // Access the specific product by index
            const product = { ...updatedProducts[index] };

            if (!row.quantity || parseFloat(row.quantity, 10) <= 0) {
              enqueueSnackbar(`Quantity cannot be empty`, { variant: 'error' });
              product.quantity = '1';
              // Update the products array with the modified product
              updatedProducts[index] = product;

              // Update the state immutably
              setPurchase(prevSales => ({
                ...prevSales,
                products: updatedProducts
              }));
            }
          }}
          onChange={(e) => handleProductChange(index, 'quantity', e.target.value)}
          value={row.quantity} size='small' />
      ),
      center: 'true'
    },
    {
      name: 'Purchase Price',
      selector: row => row.sellingPrice,
      cell: (row, index) => (
        <TextField
          type='number'
          disabled={editPurchase}
          onBlur={() => {
            // Create a copy of the products array
            const updatedProducts = [...purchase.products];

            // Access the specific product by index
            const product = { ...updatedProducts[index] };


            if (!row.sellingPrice || parseFloat(row.sellingPrice) <= 0) {
              // Update the field with the new value
              product.sellingPrice = String(1);

              // Update the products array with the modified product
              updatedProducts[index] = product;

              // Update the state immutably
              setPurchase(prevSales => ({
                ...prevSales,
                products: updatedProducts
              }));
            }
          }}
          onChange={(e) => handleProductChange(index, 'sellingPrice', e.target.value)}
          value={row.sellingPrice}
          size='small' />
      ),
      center: 'true'
    },
    {
      name: 'SGST',
      selector: row => row?.sgst,
      cell: (row, index) => (
        <TextField
          disabled={editPurchase || row.igst}
          type='number'
          onBlur={(e) => {
            // SGST CANT BE GRETER THAN 100 
            // if ( > 100) {
            //   enqueueSnackbar('SGST cannot be greater than 100!', { variant: 'error' });
            //   return;
            // }
          }}
          onChange={(e) => handleProductChange(index, 'sgst', e.target.value)}
          value={row?.sgst} size='small' />
      ),
      center: 'true'
    },
    {
      name: 'CGST',
      selector: row => row?.cgst,
      cell: (row, index) => (
        <TextField
          disabled={editPurchase || row.igst}
          type='number'
          onBlur={(e) => {
            // Create a copy of the products array
            const updatedProducts = [...purchase.products];

            // Access the specific product by index
            const product = { ...updatedProducts[index] };

            if (!row.quantity || parseFloat(row.quantity, 10) <= 0) {
              enqueueSnackbar(`Quantity cannot be empty`, { variant: 'error' });
              product.quantity = '1';
              // Update the products array with the modified product
              updatedProducts[index] = product;

              // Update the state immutably
              setPurchase(prevSales => ({
                ...prevSales,
                products: updatedProducts
              }));
            }
          }}
          onChange={(e) => handleProductChange(index, 'cgst', e.target.value)}
          value={row?.cgst} size='small' />
      ),
      center: 'true'
    },
    {
      name: 'IGST',
      selector: row => row?.igst,
      cell: (row, index) => (
        <TextField
          disabled={editPurchase || row.cgst || row.sgst}
          type='number'
          onBlur={(e) => {
            // Create a copy of the products array
            const updatedProducts = [...purchase.products];

            // Access the specific product by index
            const product = { ...updatedProducts[index] };

            if (!row.quantity || parseFloat(row.quantity, 10) <= 0) {
              enqueueSnackbar(`Quantity cannot be empty`, { variant: 'error' });
              product.quantity = '1';
              // Update the products array with the modified product
              updatedProducts[index] = product;

              // Update the state immutably
              setPurchase(prevSales => ({
                ...prevSales,
                products: updatedProducts
              }));
            }
          }}
          onChange={(e) => handleProductChange(index, 'igst', e.target.value)}
          value={row?.igst} size='small' />
      ),
      center: 'true'
    },
    {
      name: 'Net Amount',
      selector: row => {
        const quantity = parseFloat(row.quantity || 0); // Corrected parsing logic
        const sellingPrice = parseFloat(row.sellingPrice || 0); // Corrected parsing logic
        const netAmount = quantity * sellingPrice;
        return netAmount.toFixed(2);
      },
      center: 'true'
    },
    {
      name: 'Actions',
      cell: (row, index) => (
        <IconButton disabled={editPurchase} onClick={() => handleDeleteProduct(row, index)} color='error' variant='outlined'>
          <DeleteIcon />
        </IconButton>
      ),
      center: 'true'
    },
  ];



  const netTotalAmount = purchase.products.reduce((total, product) => {
    const sellingPrice = parseFloat(!product.sellingPrice ? 0 : product.sellingPrice);
    const quantity = parseFloat(!product.quantity ? 0 : product.quantity);
    const netAmount = quantity * sellingPrice;
    return total + netAmount;
  }, 0);

  const totalGSTAmount = purchase.products.reduce((total, product) => {
    const productAmount = product.sellingPrice;
    const totalGST = parseFloat(product.sgst || '0') + parseFloat(product.cgst || '0') + parseFloat(product.igst || '0');
    const gstAmount = (productAmount * totalGST) / 100;
    console.log("ðŸš€ ~ totalGSTAmount ~ gstAmount:", gstAmount)
    return total + gstAmount;
  }, 0);

  const totalAmount = netTotalAmount + totalGSTAmount;

  const filteredData = purchaseOrderList.filter((purchaseRecords) =>
    purchaseRecords?.vendorId.name?.toLowerCase().includes(searchQuery?.toLowerCase()) ||
    purchaseRecords?.invoiceNumber?.toLowerCase().includes(searchQuery?.toLowerCase())
  );

  return (
    <Container maxWidth={settings.themeStretch ? false : 'xl'}>
      <Typography variant="h4"> Purchase Order </Typography>

      {/* SUMMARY */}
      <div style={{ display: 'flex', flexDirection: 'row', justifyContent: "space-between", marginTop: "20px" }}>
        <Stack direction="row" spacing={1}>
          <Chip
            label={`Total  ${formatObj.format(purchaseSummary.totalPurchaseAmount)}`}
            variant="outlined"
          />
          <Chip
            label={`Paid  ${formatObj.format(purchaseSummary.totalPaidAmount)}`}
            variant="outlined"
          />
          <Chip
            label={`Pending  ${formatObj.format(purchaseSummary.pendingAmount)}`}
            variant="outlined"
          />
        </Stack>

        {
          user?.permissions?.purchase?.u &&
          <Stack>
            <Button variant='contained' color='primary' startIcon={<AddIcon />} onClick={handleOpen}>
              CREATE PURCHASE ORDER
            </Button>
          </Stack>
        }

      </div >



      {/* MODA */}
      <Modal
        open={open}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>

          {/* First Row */}
          <div style={{ display: 'flex', flexDirection: 'row', gap: '10px', backgroundColor: "white", padding: "15px", borderRadius: "10px" }}>
            <Autocomplete
              options={vendors}
              disabled={editPurchase}
              getOptionLabel={(option) => option.name}
              fullWidth
              value={purchase.vendor}
              onChange={(event, newValue) => {
                setPurchase((prev) => ({
                  ...prev, vendor: newValue ?? null
                }));
              }}
              renderInput={(params) => (
                <TextField {...params} label="Search Vendors" fullWidth />
              )}
            />
            <LocalizationProvider dateAdapter={AdapterDayjs} >
              <DatePicker

                label="Purchase Date"
                value={purchase.purchaseDate}
                onChange={(newValue) => {
                  setPurchase((prev) => ({ ...prev, purchaseDate: newValue }))
                }}
                format="DD/MM/YYYY"
                disabled={editPurchase}
                slotProps={{ textField: { fullWidth: true } }}
              />
            </LocalizationProvider>

            {/* <LocalizationProvider dateAdapter={AdapterDayjs}>
              <DatePicker
                label="Purchase Due Date"
                value={purchase.purchaseDueDate}
                onChange={(newValue) => {
                  setPurchase((prev) => ({ ...prev, purchaseDueDate: newValue }))
                }}
                format="DD/MM/YYYY"
                minDate={purchase.purchaseDate}
                slotProps={{ textField: { fullWidth: true } }}
              />
            </LocalizationProvider> */}

            <TextField
              label="Reference Number"
              type="text"
              fullWidth
              value={purchase.referenceNumber}
              onChange={(e) => setPurchase((prev) => ({ ...prev, referenceNumber: e.target.value }))}
            />
          </div>

          {/* Second Row */}
          <Grid container sx={{ backgroundColor: "white", padding: "15px", borderRadius: "10px", display: "flex", justifyContent: 'space-between' }} alignItems='center'>
            <Grid item xs={12} md={12} display='flex'>
              {/* <FormControl sx={{ width: "35%" }} >
                <InputLabel id="demo-simple-select-label">All Categories</InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  label='All Categories'
                  value={purchaseProduct.category}
                  onChange={(e) => {
                    setPurchaseProduct((prev) => ({ ...prev, category: e.target.value, product: null, quantity: null }))
                  }}
                  disabled={editPurchase}
                >
                  <MenuItem value='All Categories'>All Categories</MenuItem>
                  {
                    categories.map((catgy, index) => (
                      <MenuItem key={index} value={catgy.name}>{catgy.name}</MenuItem>
                    ))
                  }
                </Select>
              </FormControl> */}
              <Autocomplete
                options={products}
                value={purchaseProduct?.product ?? null}
                onChange={(event, newValue) => {
                  if (newValue) {
                    setPurchaseProduct((prev) => ({ ...prev, product: newValue }));
                    addProductToSales(newValue);
                  } else {
                    setPurchaseProduct((prev) => ({ ...prev, product: null }));
                  }
                }}
                disabled={editPurchase}
                getOptionLabel={(option) => `${option.name}`}
                onInputChange={(event, newInputValue) => {
                  console.log("ðŸš€ ~ PurchaseOrderPageView ~ newInputValue:", newInputValue)

                  setBarcodeValue(newInputValue);
                }}
                fullWidth
                renderInput={(params) => (
                  <TextField {...params} inputRef={autocompleteRef} label="Search Products" fullWidth />
                )}
              />
            </Grid>


          </Grid>


          {
            purchase.products.length > 0 &&

            <Grid container sx={{ backgroundColor: "white", padding: "15px", borderRadius: "10px", }} alignItems='center'>
              <Grid item xs={12}>
                <DataTable
                  columns={productColumns}
                  data={purchase.products}
                />
              </Grid>
            </Grid>
          }


          <Grid container display='flex' justifyContent='flex-end'>
            <Grid item xs={12} md={6} lg={6} sx={{ backgroundColor: "white", padding: "15px", borderRadius: "10px", }}>
              <div style={{ display: "flex", flexDirection: "column" }}>
                <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                  <div>GST Amount</div>
                  <div>{formatObj.format(totalGSTAmount)}</div>
                </div>

                <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                  <div>Net Amount</div>
                  <div>{
                    purchase.products.length === 0 ?
                      formatObj.format(0) :
                      formatObj.format(netTotalAmount.toFixed(2))
                  }</div>
                </div>
              </div>
            </Grid>
          </Grid>

          {/* ALL TEXTFIELD AND BANK RELATED FIELD */}
          <Grid container display='flex' justifyContent='flex-end'>
            <Grid item xs={12} md={6} lg={6} sx={{ backgroundColor: "white", padding: "15px", borderRadius: "10px", display: 'flex', flexDirection: 'column', gap: '10px' }}>
              <div style={{ display: 'flex', flexDirection: 'row', gap: '10px' }} >
                <TextField
                  fullWidth
                  disabled={editPurchase}
                  size='small'
                  label="Enter Amount"
                  onBlur={(e) => {
                    if (e.target.value > totalAmount) {
                      enqueueSnackbar('Amount to pay cannot be greater than total amount!', { variant: 'error' });
                      setPurchase((prev) => ({ ...prev, amountToPay: totalAmount.toFixed(2) }));
                    };
                  }}
                  value={purchase.amountToPay}
                  onChange={(e) => setPurchase((prev) => ({ ...prev, amountToPay: e.target.value }))}
                />

                <FormControl fullWidth size="small" >
                  <InputLabel id="demo-simple-select-label">Select Payment Status</InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    label='Select Payment Status'
                    value={purchase.paymentStatus}
                    onChange={(e) => setPurchase((prev) => ({ ...prev, paymentStatus: e.target.value }))}
                  >
                    {
                      common.paymentStatus.map((paymentStatus, index) => (
                        <MenuItem key={index} value={paymentStatus}>{paymentStatus}</MenuItem>
                      ))
                    }
                  </Select>
                </FormControl>
              </div>
              <div style={{ display: 'flex', flexDirection: 'row', gap: '10px' }} >
                <FormControl fullWidth size="small" >
                  <InputLabel id="demo-simple-select-label">Select Payment Mode</InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    label='Select Payment Mode'
                    value={purchase.paymentMode}
                    disabled={editPurchase}
                    onChange={(e) => setPurchase((prev) => ({ ...prev, paymentMode: e.target.value }))}
                  >
                    {
                      [
                        'CASH',
                        'CHEQUE',
                      ].map((paymentMode, index) => (
                        <MenuItem key={index} value={paymentMode}>{paymentMode}</MenuItem>
                      ))
                    }
                  </Select>
                </FormControl>


                <FormControl fullWidth size="small" >
                  <InputLabel id="demo-simple-select-label">Select Bank</InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    label='Select Bank'
                    value={purchase.bank}
                    disabled={editPurchase || (purchase.paymentMode === 'CASH')}
                    onChange={(e) => setPurchase((prev) => ({ ...prev, bank: e.target.value }))}
                  >
                    {
                      banks.map((bank, index) => (
                        <MenuItem key={index} value={bank._id} >{bank.bankName}</MenuItem>
                      ))
                    }
                  </Select>
                </FormControl>
              </div>
            </Grid>

          </Grid>

          <Grid container  >
            <Grid item xs={12} md={12} lg={12} sx={{ backgroundColor: "white", p: "15px", borderRadius: "10px" }} display='flex' justifyContent='space-between' alignItems='center'>
              <div style={{ display: "flex", flexDirection: 'row', justifyContent: 'space-around', gap: '20px' }}>
                <div>Total  :</div>
                <div>{purchase.products.length === 0 ?
                  formatObj.format(0) :
                  formatObj.format(totalAmount)
                }</div>
              </div>
              <div style={{ display: 'flex', gap: '10px' }}>

                {
                  editPurchase ?
                    <Button variant='contained'
                      onClick={handleUpdatePurchaseOrder}
                      color='primary' disabled={
                        !purchase.paymentStatus
                      } >UPDATE</Button>

                    :
                    <Button variant='contained'
                      onClick={handleSavePurchaseOrder}
                      color='primary' disabled={
                        !purchase.vendor ||
                        purchase.products.length === 0 ||
                        !purchase.amountToPay ||
                        !purchase.paymentMode ||
                        !purchase.paymentStatus ||
                        (
                          purchase.paymentMode !== 'CASH' && !purchase.bank
                        )
                      } >SAVE</Button>
                }




                <Button variant='contained' color='error' onClick={handleClose}>CANCEL</Button>
              </div>
            </Grid>
          </Grid>

        </Box>
      </Modal>

      <div style={{ marginTop: "20px" }} >
        <div style={{ display: "flex", flexDirection: 'row', alignItems: "center", gap: "10px" }}>
          <div>
            <TextField
              placeholder='Search'
              size='small'
              value={searchQuery}
              onChange={e => setSearchQuery(e.target.value)}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <SearchIcon />
                  </InputAdornment>
                ),
              }}
            />
          </div>
          <div>
            <LocalizationProvider dateAdapter={AdapterDayjs} >
              <Box sx={{ display: 'flex', flexDirection: 'row', gap: '10px', alignItems: 'center' }}>
                <DatePicker
                  label="From"
                  format='DD/MM/YYYY'
                  value={startDate}
                  slotProps={{ textField: { size: 'small' } }}
                  onChange={handleStartDateChange}
                  renderInput={(props) => <TextField {...props} />}
                />
                <Typography>-</Typography>
                <DatePicker
                  label="To"
                  format='DD/MM/YYYY'
                  value={endDate}
                  slotProps={{ textField: { size: 'small' } }}
                  onChange={handleEndDateChange}
                  renderInput={(props) => <TextField {...props} />}
                />
              </Box>
            </LocalizationProvider>
          </div>
        </div>


        <div style={{ marginTop: "20px" }}>
          <DataTable
            columns={columns}
            data={filteredData}
            progressPending={isPurchasePageLoading}
            pagination
          />
        </div>
      </div>

      {/* RECORD PAYMENT MODAL  */}
      <Modal id="paymentModal"
        open={recordPaymentModal}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={styleForRecordPayment}>
          <Grid container sx={{ display: 'flex', flexDirection: 'column', gap: '10px' }} >
            <Grid item xs={12}>
              <Typography variant='h6'>Customer Details</Typography>
            </Grid>
            <Grid item xs={12}>
              <TextField
                label='Name'
                value={recordPayment.name}
                disabled
                sx={{ backgroundColor: 'white', borderRadius: '7px' }}
                fullWidth
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                label='Balance'
                value={recordPayment.balance}
                disabled
                sx={{ backgroundColor: 'white', borderRadius: '7px' }}
                fullWidth
              />
            </Grid>
            <Grid item xs={12}>
              <Typography variant='h6'>Payment Details</Typography>
            </Grid>
            <Grid item xs={12}>
              <TextField
                label='Amount To Pay'
                value={recordPayment.amountToPay}
                onBlur={
                  () => {
                    if (recordPayment.amountToPay > recordPayment.balance) {
                      enqueueSnackbar('Amount to pay cannot be greater than balance!', { variant: 'error' });
                      setRecordPayment((prev) => ({ ...prev, amountToPay: recordPayment.balance }));
                    }
                  }
                }
                onChange={(e) => setRecordPayment((prev) => ({ ...prev, amountToPay: e.target.value }))}
                sx={{ backgroundColor: 'white', borderRadius: '7px' }}
                fullWidth
              />
            </Grid>

            <Grid item xs={12} >
              <LocalizationProvider dateAdapter={AdapterDayjs} >
                <DatePicker

                  label="Payment Date"
                  value={recordPayment.paymentDate}
                  maxDate={dayjs(new Date())}
                  onChange={(newValue) => {
                    setRecordPayment((prev) => ({ ...prev, paymentDate: newValue }))
                  }}
                  sx={{ backgroundColor: 'white', borderRadius: '7px' }}
                  format="DD/MM/YYYY"
                  slotProps={{ textField: { fullWidth: true } }}
                />
              </LocalizationProvider>
            </Grid>
            <Grid item xs={12}>
              <FormControl fullWidth >
                <InputLabel id="record-payment-mode">Select Payment Mode</InputLabel>
                <Select
                  labelId="record-payment-mode"
                  id="demo-simple-select"
                  label='Select Payment Mode'
                  sx={{ backgroundColor: 'white', borderRadius: '7px' }}
                  value={recordPayment.paymentMode}
                  onChange={(e) => setRecordPayment((prev) => ({ ...prev, paymentMode: e.target.value }))}
                >
                  {
                    [
                      'CASH',
                      'CHEQUE',
                    ].map((paymentMode, index) => (
                      <MenuItem key={index} value={paymentMode}>{paymentMode}</MenuItem>
                    ))
                  }
                </Select>
              </FormControl>
            </Grid>
            {
              recordPayment.paymentMode !== 'CASH' &&
              <Grid item xs={12}>
                <FormControl fullWidth >
                  <InputLabel id="select-bank">Select Bank</InputLabel>
                  <Select
                    labelId="select-bank"
                    id="demo-simple-select"
                    label='Select Bank'
                    value={recordPayment.bank}
                    sx={{ backgroundColor: 'white', borderRadius: '7px' }}
                    onChange={(e) => setRecordPayment((prev) => ({ ...prev, bank: e.target.value }))}
                  >
                    {
                      banks.map((bank, index) => (
                        <MenuItem key={index} value={bank._id} >{bank.bankName}</MenuItem>
                      ))
                    }
                  </Select>
                </FormControl>
              </Grid>
            }
            <Grid item xs={12} sx={{ display: 'flex', justifyContent: 'flex-end', gap: '10px' }}>
              <Button
                variant='contained'
                color='primary'
                onClick={handleRecordPayment}
                disabled={
                  !recordPayment.amountToPay ||
                  !recordPayment.paymentMode ||
                  (recordPayment.paymentMode !== 'CASH' && !recordPayment.bank)
                }
              >
                Update Payment
              </Button>
              <Button
                variant='contained'
                color='error'
                onClick={() => {
                  setRecordPaymentModal(false);
                  setRecordPayment(initialRecordPaymentState);
                }}
              >
                Cancel
              </Button>

            </Grid>

          </Grid>
        </Box>
      </Modal>



      {/* Dialog For Delete */}
      <Dialog open={openDialog} onClose={() => setOpenDialog(false)}>
        <DialogTitle>Remove</DialogTitle>
        <DialogContent>
          Are you sure you want to delete this purchase order?
        </DialogContent>
        <DialogActions>
          <Button onClick={() => {
            setOpenDialog(false);
            setTempData({});
          }}>Cancel</Button>
          <Button onClick={handleConfirmDelete} variant="contained" color="error">
            Delete
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
}
